.cta-satisfied {
  padding-left: 129px;
}
.single-satisfied h1 {
  color: #877fc2;
  float: left;
  width: 200px;
  margin-right: 20px;
}
.single-satisfied h5 {
  color: #fff;
  margin-bottom: 15px;
}
.single-satisfied h5 i {
  margin-right: 5px;
}

.font-size-50 {
  font-size: 50px;
}
.font-size-40 {
  font-size: 40px;
}
.single-satisfied p {
  color: #899dab;
}

.single-satisfied h5,
.single-satisfied p {
  width: calc(100% - 160px);
  display: inline-block;
}

/* fact h5*/
.h5fact-wrapper > i {
  display: block;
  font-size: 40px;
  color: $seconderyMain;
  margin-bottom: 25px;
}
.h5fact-wrapper > span {
  font-size: 40px;
  font-weight: 600;
  color: $primaryMain;
  font-family: "Poppins", sans-serif;
  margin-bottom: 3px;
  display: block;
}
.h5fact-wrapper > h5 {
  font-size: 14px;
  font-weight: 500;
  color: #223645;
  margin-bottom: 15px;
}
.h5fact-wrapper {
  padding-right: 30px;
}
.h5fact-border {
  border-bottom: 2px solid #f1f1f1;
}
